<template>
  <div v-loading="loading" element-loading-text="拼命加载中">
    <div class="handle-box">
      <!-- <div class="btn">
        <el-input
          placeholder="请输入用户ID/昵称"
          v-model="user_id"
          @keyup.enter.native="search(user_id)"
          class="input-with-select"
        >
          <el-button
            slot="append"
            icon="el-icon-search"
            @click="search(user_id)"
          ></el-button>
        </el-input>
      </div> -->
      <div class="btn">
        <el-input
          placeholder="请输入用户ID/昵称"
          v-model="user_id"
          @keyup.enter.native="search(user_id)"
          class="input-with-select"
        >
          <el-select
            v-model="type"
            placeholder="请选择搜索方式"
            slot="prepend"
            @change="changeType"
            style="width: 150px"
          >
            <el-option
              v-for="item in typeList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
          <el-button
            slot="append"
            icon="el-icon-search"
            @click="search(user_id)"
          ></el-button>
        </el-input>
      </div>
    </div>

    <!-- 表格数据 -->

    <el-table
      :data="tableData"
      border
      :header-cell-style="{ background: '#f0f0f0' }"
      max-height="620"
    >
      <el-table-column prop="id" label="用户ID"></el-table-column>
      <el-table-column prop="avatarUrl" label="头像">
        <template slot-scope="scope">
          <img
            :src="scope.row.avatarUrl"
            style="max-width: 180px; max-height: 80px"
          />
        </template>
      </el-table-column>
      <el-table-column prop="nickname" label="昵称"></el-table-column>
      <el-table-column prop="name" label="名称"></el-table-column>
      <el-table-column prop="sex" label="性别" width="120px">
        <template slot-scope="scope">
          <span v-text="scope.row.sex == 1 ? '男' : '女'"></span>
        </template>
      </el-table-column>
      <el-table-column prop="phone" label="手机号码"></el-table-column>
      <el-table-column prop="mandate.created_at" label="是否已授权">
        <template slot-scope="scope">
          <span v-text="scope.row.mandate == null ? '未授权' : '已授权'"></span>
        </template>
      </el-table-column>
      <el-table-column
        prop="mandate.created_at"
        label="授权时间"
      ></el-table-column>
      <el-table-column label="操作" width="120px">
        <template slot-scope="scope">
          <el-dropdown>
            <el-button type="primary">
              操作
              <i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item>
                <el-button
                  size="mini"
                  type="primary"
                  @click="handleOpenPermission(scope.$index, scope.row)"
                  >开通权限</el-button
                >
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button
                  size="mini"
                  type="primary"
                  @click="handleBanUsePermission(scope.$index, scope.row)"
                  >禁止使用权限</el-button
                >
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button
                  size="mini"
                  type="primary"
                  @click="handleUser(scope.$index, scope.row)"
                  >查看详情</el-button
                >
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button
                  size="mini"
                  type="primary"
                  @click="handleOrder(scope.$index, scope.row)"
                  >订单列表</el-button
                >
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页 -->
    <div class="block">
      <el-pagination
        @current-change="currentChange"
        :current-page.sync="current"
        :page-size="size"
        layout="sizes, prev, pager, next, jumper"
        :total="total"
        @size-change="sizeChange"
      ></el-pagination>
    </div>

    <el-dialog
      title="开通权限"
      :visible.sync="dialogOpenPermission"
      :close-on-click-modal="false"
      width="500px"
      @close="closePermissionForm"
    >
      <div class="box">
        <el-form :model="openPermissionForm">
          <div v-for="(item, index) in openPermissionForm.service" :key="index">
            <el-form-item label="选择学校">
              <el-select
                v-model="item.school_id"
                placeholder="请选择学校"
                @change="schoolChange"
              >
                <el-option
                  v-for="item in schools"
                  :label="item.name"
                  :value="item.id"
                  :key="item.index"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="选择商品">
              <el-select v-model="item.product_id" placeholder="选择商品">
                <el-option
                  v-for="item in products"
                  :label="item.title"
                  :value="item.id"
                  :key="item.index"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="选择学生">
              <el-select v-model="item.student_id" placeholder="选择学生">
                <el-option
                  v-for="item in students"
                  :label="item.name"
                  :value="item.id"
                  :key="item.index"
                ></el-option>
              </el-select>
            </el-form-item>
          </div>
          <el-form-item label="操作">
            <el-button type="primary" @click="addopenPermissionForm"
              >添加</el-button
            >
            <el-button type="primary" @click="delopenPermissionForm"
              >删除</el-button
            >
          </el-form-item>
          <div class="submit">
            <el-form-item>
              <el-button type="primary" @click="newOpenPermission"
                >提交</el-button
              >
            </el-form-item>
          </div>
        </el-form>
      </div>
    </el-dialog>

    <el-dialog
      title="禁止使用权限"
      :visible.sync="dialogBanUsePermission"
      :close-on-click-modal="false"
    >
      <div class="box">
        <el-form>
          <el-form-item label="选择禁用的学校">
            <el-transfer
              v-model="banSchoolList"
              :data="schoolList"
              :titles="['选择学校', '禁用学校']"
              :button-texts="['取消', '禁用']"
              @left-check-change="changeLeft"
              @right-check-change="changeRight"
              @change="newBanUsePermission"
            ></el-transfer>
          </el-form-item>
          <div class="submit"></div>
        </el-form>
      </div>
    </el-dialog>

    <!-- 查看详情 -->
    <el-dialog
      :visible.sync="dialogUser"
      title="用户详情"
      width="500px"
      :close-on-click-modal="false"
    >
      <el-form :model="form" label-width="80px">
        <el-form-item label="性别">
          <el-radio-group v-model="form.sex">
            <el-radio :label="1">男</el-radio>
            <el-radio :label="2">女</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="姓名">
          <el-input v-model="form.name" :disabled="disabled"></el-input>
        </el-form-item>
        <el-form-item label="手机号">
          <el-input v-model="form.phone" :disabled="disabled"></el-input>
        </el-form-item>
        <el-form-item label="地址">
          <el-input
            v-model="form.address"
            type="textarea"
            :disabled="disabled"
          ></el-input>
        </el-form-item>
      </el-form>
    </el-dialog>

    <el-dialog title="订单列表" :visible.sync="dialogServiceOrder" width="80%">
      <div class="box">
        <div>
          <el-table
            :data="orderData"
            border
            :header-cell-style="{ background: '#f0f0f0' }"
            max-height="620"
          >
            <el-table-column prop="no" label="订单ID"></el-table-column>
            <el-table-column
              prop="user_id"
              label="用户ID"
              width="150"
            ></el-table-column>
            <el-table-column
              prop="user_name"
              label="用户名"
              width="150"
            ></el-table-column>
            <el-table-column
              prop="name"
              label="商品名称"
              width="300"
            ></el-table-column>
            <el-table-column
              prop="price"
              label="商品价格"
              width="150"
            ></el-table-column>
            <!-- <el-table-column prop="time" label="商品时长"></el-table-column> -->
            <el-table-column prop="status" label="订单状态" width="150">
              <template slot-scope="scope">
                <span v-if="scope.row.status == 1">提交</span>
                <span v-else-if="scope.row.status == 2">已付款</span>
                <span v-else-if="scope.row.status == 3">无效</span>
                <span v-else-if="scope.row.status == 4">已退款</span>
              </template>
            </el-table-column>
            <el-table-column
              prop="created_at"
              label="创建时间"
            ></el-table-column>
            <el-table-column label="操作" width="120px">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="primary"
                  @click="handleRefund(scope.$index, scope.row)"
                  >申请退款</el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
    </el-dialog>

    <!-- 删除提示框 -->
    <el-dialog
      :visible.sync="dialogRefund"
      title="申请退款"
      width="20%"
      align="center"
      :close-on-click-modal="false"
    >
      <div style="font-size: 20px; margin-bottom: 30px">
        退款成功, 将无法使用服务, 是否申请退款
      </div>
      <el-input
        placeholder="请输入退款金额"
        v-model="price"
        class="input-with-select input"
        @change="inputPrice"
      >
      </el-input>
      <span>
        <el-button type="primary" @click="toRefund">确定</el-button>
        <el-button type="danger" @click="dialogRefund = false">取消</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/index.js";
export default {
  name: "user",
  data() {
    return {
      loading: true,
      tableData: [],
      dialogOpenPermission: false, // 开通权限
      schools: [], // 开通权限--学校
      products: [], // 开通权限--商品
      students: [], // 开通权限--学生
      schoolList: [],
      id: "", // 用户id
      cancalSchool: [], // 取消学校
      banSchool: [], // 禁用学校
      goodsList: [],
      banSchoolList: [],
      openPermissionForm: {
        user_id: "",
        service: [],
      },
      dialogBanUsePermission: false, // 禁止使用权限

      activeName: "school",
      user_id: "", // 搜索用户id
      nickname: '',
      type: 1, // 选中的搜索方式
      typeList: [
        {
          // 搜索方式
          value: 1,
          label: "按用户ID搜索",
        },
        {
          value: 2,
          label: "按昵称搜索",
        },
      ],
      typeDisabled: false,
      current: 1, // 分页
      total: 0,
      size: 10,
      mode: 2,
      dialogUser: false,
      form: {},
      disabled: false,
      orderData: [],
      dialogServiceOrder: false,
      dialogRefund: false,
      product_id: "",
      price: "",
      status: "",
      pro_price: "",
    };
  },
  mounted() {
    this.getWxUser();
    // this.getSchool()
  },
  methods: {
    // 获取表格数据
    getWxUser() {
      var self = this;
      self.loading = true;
      API.wxUser(self.current)
        .then((res) => {
          self.loading = false;
          self.tableData = res.data;
          self.total = res.total;
        })
        .catch((err) => {
          self.loading = false;
        });
    },
    // 分页
    currentChange(val) {
      var self = this;
      self.current = val;
      self.loading = true;
      API.wxUser(val, self.size)
        .then((res) => {
          self.loading = false;
          self.tableData = res.data;
          self.total = res.total;
        })
        .catch((err) => {
          self.loading = false;
        });
    },
    sizeChange(val) {
      var self = this;
      self.size = val;
      self.loading = true;
      API.wxUser(self.current, val)
        .then((res) => {
          self.loading = false;
          self.tableData = res.data;
          self.total = res.total;
          self.current = 1;
        })
        .catch((err) => {
          self.loading = false;
        });
    },

    // 选择搜索方式
    changeType(val) {
      var self = this;
      self.typeDisabled = true;
      self.user_id = "";
    },
    // 搜索
    search() {
      var self = this;
      self.loading = true;
      if (self.type == 1) {
        var user_id = self.user_id;
        API.userSearch(user_id, nickname).then((res) => {
          self.loading = false;
          self.tableData = res.data;
          self.total = res.total;
          self.$message.success("搜索成功！");
        });
      } else {
        var nickname = self.user_id;
        API.userSearch(user_id, nickname).then((res) => {
          self.loading = false;
          self.tableData = res.data;
          self.total = res.total;
          self.$message.success("搜索成功！");
        });
      }
    },

    // 禁用权限
    handleBanUsePermission(index, row) {
      var self = this;
      self.dialogBanUsePermission = true;
      self.id = row.id;
      self.schoolList = [];
      self.banSchoolList = [];
      API.banProducts(self.id).then((res) => {
        res.forEach((item) => {
          self.schoolList.push({
            label: item.name,
            key: item.id,
            type: item.type,
          });

          if (item.type === 1) {
            self.banSchoolList.push(item.id);
          }
        });
      });
    },
    // 禁用权限--左边
    changeLeft(val) {
      var self = this;
      self.banSchool = val;
    },
    // 禁用权限--右边
    changeRight(val) {
      var self = this;
      self.cancalSchool = val;
    },
    newBanUsePermission(val, direction) {
      var self = this;
      var cancalform = {
        user_id: self.id,
        type: 2,
        school_id: self.cancalSchool,
      };

      if (direction === "left") {
        API.banProduct(cancalform).then((res) => {
          self.$message.success("提交成功");
        });
      }

      var banform = {
        user_id: self.id,
        type: 1,
        school_id: self.banSchool,
      };
      if (direction === "right") {
        API.banProduct(banform).then((res) => {
          self.$message.success("提交成功");
        });
      }

      self.dialogBanUsePermission = false;
    },

    // 开通权限
    handleOpenPermission(index, row) {
      var self = this;
      self.dialogOpenPermission = true;
      self.addopenPermissionForm();
      // self.user_id = row.id;
      API.jurisdictionSchool(1, 100, self.user_id, self.mode).then((res) => {
        res.data.forEach((item) => {
          self.schools = res.data;
        });
      });
    },
    // 获取商品列表
    getGoods(value) {
      var self = this;
      API.goods(value).then((res) => {
        self.goodsList = res.data;
      });
    },
    schoolChange(value) {
      var self = this;
      self.getGoods(self.openPermissionForm.school);
      self.school_id = value;
      API.jurisdictionProducts(self.school_id).then((res) => {
        self.products = res;
      });
      API.jurisdictionStudent(self.user_id, self.school_id).then((res) => {
        self.students = res;
      });
    },
    addopenPermissionForm() {
      var self = this;
      self.openPermissionForm.service.push({});
    },
    delopenPermissionForm() {
      var self = this;
      self.openPermissionForm.service.pop({});
    },
    closePermissionForm() {
      var self = this;
      self.delopenPermissionForm();
      self.openPermissionForm.service = [];
    },

    // 提交
    newOpenPermission() {
      var self = this;
      self.openPermissionForm.user_id = self.user_id;
      API.setProduct(self.openPermissionForm).then((res) => {
        self.$message.success("提交成功"),
          (self.openPermissionForm = {
            user_id: "",
            service: [],
          });
        self.dialogOpenPermission = false;
      });
    },

    handleUser(index, row) {
      var self = this;
      self.disabled = true;
      console.log(row);
      if (row.userInfo) {
        self.form = row.userInfo;
        self.dialogUser = true;
      } else {
        self.$message.warning("该用户未填写信息!");
      }
    },
    handleOrder(index, row) {
      var self = this;
      // self.user_id = row.id;
      self.dialogServiceOrder = true;
      API.userOrder(self.user_id).then((res) => {
        console.log(res);
        self.$message.success("获取数据成功");
        self.orderData = res;
      });
    },
    // 申请退款
    handleRefund(index, row) {
      var self = this;
      self.dialogRefund = true;
      console.log(row);
      self.product_id = row.id;
      self.status = row.status;
      self.pro_price = row.price;
      self.price = "";
    },
    inputPrice(value) {
      self.price = value;
    },
    toRefund() {
      var self = this;
      if (!self.product_id) {
        self.$message.warning("订单付款不成功, 无法申请退款");
      } else if (self.status == 4) {
        self.$message.warning("订单已经退款, 请勿重复提交");
      } else if (self.pro_price < self.price) {
        self.$message.warning("退款金额大于商品金额, 无法退款");
      } else {
        const loading = self.$loading({
          lock: true,
          text: "退款中...",
          spinner: "el-icon-loading",
          background: "rgba(0, 0, 0, 0.7)",
        });
        API.refund(self.product_id, self.price)
          .then((res) => {
            loading.close();
            self.$message.success("退款成功");
            self.dialogRefund = false;
            self.price = "";
            API.userOrder(self.user_id).then((res) => {
              console.log(res);
              self.orderData = res;
            });
          })
          .catch((err) => {
            loading.close();
          });
      }
    },
  },
};
</script>

<style scoped>
.input {
  margin-bottom: 20px;
}
</style>
